.legal-text-content {
  padding: 12px 32px;
  background: linear-gradient(to right, #ecf2ff, #fbfcff);
}
/* Title */
.legal-siteTitle a {
  text-decoration: none;
  color: #54de54;
  font-size: 48px;
  letter-spacing: 0.8px;
}
.legal-logo {
  margin-top: 5px;
  padding-top: 12px;
  height: 50px;
}
.legal-siteTitle {
  padding: 0 0 8px 0;
  border-radius: 2px;
  background-color: white;
  text-align: center;
  font-family: "Poppins", sans-serif;
}
.legal-siteSign {
  color: #54de54;
  font-family: Cambria, sans-serif;
  font-size: 56px;
  font-weight: bold;
}
/* Info */
.legal-title {
  margin: 16px 0;
  padding: 0 0 2px 0;
  border-bottom: 2px solid grey;
  font-family: "Poppins", sans-serif;
  font-size: 32px;
  font-weight: bold;
  letter-spacing: 0.8px;
}
.legal-description {
  margin: 0 0 16px 0;
  font-family: "Rubik", sans-serif;
  font-size: 18px;
  letter-spacing: 0.8px;
  line-height: 1.8rem;
}
/* Footer */
.legal-footer {
  padding: 24px 40px;
  display: block;
  color: black;
  font-family: "Rubik", sans-serif;
  font-size: 18px;
  font-weight: bold;
  letter-spacing: 0.8px;
  line-height: 1.6rem;
}
/* Responsive */
@media screen and (max-width: 600px) {
  .legal-section {
    padding: 24px;
  }
  .legal-title {
    font-size: 24px;
  }
  .legeal-description {
    font-size: 16px;
  }
  .legal-footer {
    padding: 12px 20px;
    text-align: center;
    font-size: 16px;
  }
}
